import React, {Component} from 'react';
import "./milligram.css";
import emailjs from "emailjs-com";

const Formu = () => {

        function sendEmail(e) {
            e.preventDefault();
        
            emailjs.sendForm('gmail', 'my_email', e.target, 'user_lxDoyl1kZzxMBWZjskJ4K')
              .then((result) => {
                  console.log(result.text);
              }, (error) => {
                  console.log(error.text);
              });
              e.target.reset()
          }
        

    return(
        <div className="form_pad">
            <div className="projet_titre">Contact</div>

            <div>
                <div className="form_bord">
                    <form onSubmit={sendEmail} >
                        <div className="input_padding">
                            <input type="email" placeholder="Adresse email" name="mail" required="true"/>
                        </div>
                        <div className="input_padding">
                            <input type="text" placeholder="Sujet" name="sujet" required="true"/>
                        </div>
                        <div className="input_padding_textarea">
                            <textarea placeholder="Message" name="message" required="true" rows="10"/>
                        </div>
                        <div className="input_padding">
                            <input className="bouton" type="submit" value="Envoyer"/>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

class Form extends Component{
    render(){
        return(
            <Formu />
        )
    }
}

export default Form;